import { usersAPIComponent } from '../API/API'
import { updateObjectInArray } from '../utils/object-helper/object-helper'

let initialState = {
    users: [],
    totalCount: 0,
    pageSize: 20,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [],
    portionSize:15,
    currentPortion:1
}
const FOLLOW = 'users/FOLLOW',
    UNFOLLOW = 'users/UNFOLLOW',
    SET_USERS = 'users/SET_USERS',
    SET_CURRENT_PAGE = 'users/SET_CURRENT_PAGE',
    SET_USERS_COUNT = 'users/SET_USERS_COUNT',
    SET_CURRENT_PORTION = 'SET_CURRENT_PORTION',
    TOGGLE_IS_FETCHING = 'users/TOGGLE_IS_FETCHING',
    TOGGLE_IS_FOLLOWING_PROGRESS = 'users/TOGGLE_IS_FOLLOWING_PROGRESS',

    usersReducer = (state = initialState, action) => {
        switch (action.type) {
            case FOLLOW:
                return (
                    {
                        ...state,
                        users: updateObjectInArray(state.users,action.userID,"id",{followed:true})
                    }
                )
            case UNFOLLOW:
                return (
                    {
                        ...state,
                        users: updateObjectInArray(state.users,action.userID,"id",{followed:false})
                    }
                )
            case SET_USERS:
                return {...state, users: action.users}
            case SET_CURRENT_PAGE:
                return {...state, currentPage: action.currentPage}
            case SET_USERS_COUNT:
                return {...state, totalCount: action.totalCount}
            case SET_CURRENT_PORTION:
                return {...state, currentPortion: action.currentPortion}
            case TOGGLE_IS_FETCHING:
                return {...state, isFetching: action.isFetching}
            case TOGGLE_IS_FOLLOWING_PROGRESS:
                return {
                    ...state,
                    followingInProgress: action.isFetching ?
                        [...state.followingInProgress, action.userID] :
                        [state.followingInProgress.filter(id => id !== action.userID)]
                }
            default :
                return state
        }
    },
    followUnfollowFlow = async (dispatch, userID, apiMethod, actionCreator) => {
        dispatch(toggleFollowingProgress(true, userID))
        let data = await apiMethod(userID)
        if (data.resultCode === 0) {
            dispatch(actionCreator(userID))
        }
        dispatch(toggleFollowingProgress(false, userID))
    }
export const follow = (userId) => ({type: FOLLOW, userID: userId}),
    unfollow = (userId) => ({type: UNFOLLOW, userID: userId}),
    setUsers = (user) => ({type: SET_USERS, users: user}),
    setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage}),
    setUsersCount = (totalCount) => ({type: SET_USERS_COUNT, totalCount: totalCount}),
    toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching: isFetching}),
    setCurrentPortion = (currentPortion) => ({type:SET_CURRENT_PORTION,currentPortion}),
    toggleFollowingProgress = (isFetching, userID) => ({
        type: TOGGLE_IS_FOLLOWING_PROGRESS,
        isFetching: isFetching,
        userID: userID
    }), getUsersFromServer = (currentPage, pageSize) => async (dispatch) => {
        dispatch(toggleIsFetching(true))
        let data = await usersAPIComponent.getUsersFromServer(currentPage, pageSize)
        dispatch(toggleIsFetching(false))
        dispatch(setUsers(data.items))
        dispatch(setUsersCount(data.totalCount))
    }, unfollowUser = (userID) => async (dispatch) => {
        followUnfollowFlow(dispatch, userID,usersAPIComponent.unfollowUser.bind(usersAPIComponent),unfollow)
    }, followUser = (userID) => async (dispatch) => {
        followUnfollowFlow(dispatch, userID,usersAPIComponent.followUser.bind(usersAPIComponent),follow)
    }

export default usersReducer