import React from 'react'
import s from './Contacts.module.css'

const Contacts = (props) => {
    let contactsArr = []
    for (let key in props.contacts) {
        if (props.contacts[key]) {
            contactsArr.push(
                <div>
                    <label>{key}</label>
                    <div><a href={'https://' + props.contacts[key]}>{`${props.contacts[key]}`}</a></div>
                </div>
            )
        }
        if (contactsArr[0] === null){return <div>Нет контактов</div>}
    }
    return <div className={s.contacts}>
        <base target="_blank"/>
        {contactsArr}
        </div>
}

export default Contacts