import React from 'react'
import s from './ProfileInfo.module.css'
import Preloader from '../../Common/Preloader/Preloader'
import demonJPG from './../../../assets/images/demon-face.jpg'
import Contacts from './Contacts/Contacts'
import ProfileStatus from './ProfileStatus'

const ProfileInfo = (props) => {
    if (!props.profile) {
        return <Preloader/>
    }
    return (
        <div className={s.profile}>
            <span className={s.avatarPhoto}>
                <img src={props.profile.photos.large ? props.profile.photos.large : demonJPG}/>
            </span>
            <span className={s.profileInfo}>
                    <div className={s.profileName}>{props.profile.fullName}</div>
                <div className={s.profileAboutMe}>
                    <ProfileStatus status={props.status} updateUserStatus={props.updateUserStatus}/>
                </div>
                    <div className={s.profileAboutMe}>
                        {props.profile.aboutMe ?
                            'О себе: ' + props.profile.aboutMe :
                            'О себе ничего сказать не могу'}
                    </div>
                    <div className={s.profileLookingForAJob}>
                        {props.profile.lookingForAJob ?
                            `Я ищу работу: ${props.profile.lookingForAJobDescription}` :
                            'Я не ищу работу'}
                    </div>
                    <div> {'Контакты: '}
                        <Contacts contacts={props.profile.contacts}/>
                    </div>
                </span>
        </div>
    )
}

export default ProfileInfo