import { profileAPIComponent } from '../API/API'

let initialState = {
    myPosts: [
        {id: 1, message: 'Hi, fuck you!', likesCount: 15},
        {id: 2, message: 'It\'s my first time', likesCount: 20},
        {id: 3, message: 'I\'m so tired', likesCount: 1200},
        {id: 4, message: 'HOLY SHIT!!', likesCount: 1},
        {id: 5, message: 'You son of the bitch!', likesCount: 20}
    ],
    profile: null,
    status: ''
}
const ADD_POST = 'profile/ADD_POST',
    DELETE_POST = 'profile/DELETE_POST',
    SET_USERS_PROFILE = 'profile/SET_USERS_PROFILE',
    SET_USER_STATUS = 'profile/SET_USER_STATUS',

    profileReducer = (state = initialState, action) => {
        switch (action.type) {
            case ADD_POST :
                let newPost = {
                    id: 6,
                    message: action.text,
                    likesCount: 0
                }
                return {
                    ...state,
                    myPosts: [...state.myPosts, newPost]
                }
            case SET_USERS_PROFILE:
                return {
                    ...state,
                    profile: action.profile
                }
            case SET_USER_STATUS:
                return {
                    ...state,
                    status: action.status
                }
            case DELETE_POST:
                return {
                    ...state,
                    myPosts: state.myPosts.filter(p => p.id !== action.postID)
                }
            default :
                return state
        }
    }

export const addPost = (text) => ({type: ADD_POST, text}),
    deletePost = (postID) => ({type: DELETE_POST, postID}),
    setUsersProfile = (profile) => ({type: SET_USERS_PROFILE, profile}),
    setUserStatus = (status) => ({type: SET_USER_STATUS, status}),

    getUserProfile = (userID) => async (dispatch) => {
        let profile = await profileAPIComponent.getUserProfile(userID)
        dispatch(setUsersProfile(profile))
    },
    getUserStatus = (userID) => async (dispatch) => {
        let status = await profileAPIComponent.getUserStatus(userID)
        dispatch(setUserStatus(status))
    },
    updateUserStatus = (status) => async (dispatch) => {
        let response = await profileAPIComponent.updateUserStatus(status)
        if (response.resultCode === 0) {
            dispatch(setUserStatus(status))
        }
    }, putProfileSettings = (form, profile) => async (dispatch) => {
        let putProfile = {
                ...profile,
                ...form
            },
            response = await profileAPIComponent.putUserProfile(putProfile)
        if (response.resultCode === 0) {
            dispatch(getUserProfile(profile.userId))
        }
    }, uploadPhoto = (photo) => async (dispatch) => {
        let formData = new FormData()
        formData.append('image', photo)
        await profileAPIComponent.postUserPhoto(formData)
    }

export default profileReducer